@import url('https://fonts.googleapis.com/css?family=Miriam+Libre:400,700|Roboto:400,500,700|Overpass+Mono:300');

$warm-light-grey: #F4F3F1;
$dark-grey: #505050;

.comp-form {
    .logo {
        position: fixed;
        z-index: 1000;
        top: 16px;
        left: 18px;
        cursor: pointer;
    }




    .scroll-out {
        height: 80vh;
    }
}



b,
strong {
    font-family: "Roboto";
    font-weight: 700;
    color: $dark-grey;
}

body hr {
    border: 0.5px solid #979797;
    margin: 0;
    margin-top: 0.3rem;
}

.fa-caret-left:before {
    margin-right: 7px;
}

.fa-caret-right:before {
    margin-left: 7px;
}


///////////////////////////
/// EACH CHAPTER/TOPIC
.content {
    position: absolute;
    top: 0;
    width: 100%;
    margin: 0;
    padding: 0;
    padding-bottom: 200px;
}

.content {

    header {
        position: absolute;
        font-family: Roboto-Bold;
        font-size: 12px;
        color: $dark-grey;
        letter-spacing: 0.23px;
        width: 100%;
        max-width: 1200px;
        margin: 0 auto;
        padding: 20px 0 0 0;
        z-index: 100;
    }

    header span {
        display: inline-block;
        width: 17%;
    }

    header span:first-child {
        margin-left: 16%;
        width: 48%;
    }

    .wrapper {
        width: 100%;
        max-width: 1200px;
        margin: 0 auto;
    }

    .topic-text {
        width: 100%;
        padding: 0 20% 10px 16%;
    }

    .hero {
        background-color: $warm-light-grey;
        padding-top: 180px;
        padding-bottom: 133px;
        position: relative;
        top: -180px;
    }

    .hero p {
        font-family: "Miriam Libre";
        font-weight: bold;
        font-size: 30px;
        color: $dark-grey;
        letter-spacing: 0.31px;
        line-height: 54px;
        margin-top: 32px;
        margin-bottom: 80px;
    }

    h1 {
        font-family: 'Miriam Libre';
        font-weight: bold;
        font-size: 36px;
        color: $dark-grey;
        letter-spacing: 0.56px;
        text-transform: uppercase;
        margin-top: 180px;
    }

    h2 {
        font-family: "Miriam Libre";
        font-weight: bold;
        font-size: 25px;
        color: $dark-grey;
        letter-spacing: 0.31px;
        margin-top: 100px;
        margin-bottom: 24px;
    }

    h2:first-child {
        margin-top: 0;
    }

    h2 span {
        padding-left: 56px;
    }

    h3 {
        font-family: 'Miriam Libre';
        font-size: 24px;
        color: $dark-grey;
        letter-spacing: 0.31px;
        line-height: 25px;
        margin-top: 52px;
        margin-bottom: 15px;
    }

    h3.first {
        margin-top: 0px;
    }

    h4 {
        font-family: "Roboto";
        font-weight: 700;
        ;
        font-size: 16px;
        color: $dark-grey;
        letter-spacing: 0.31px;
        line-height: 25px;
        margin-top: 45px;
    }

    h5 {
        font-family: "Miriam Libre";
        font-weight: bold;
        font-size: 10px;
        color: $dark-grey;
        letter-spacing: 2.41px;
        text-transform: uppercase;
    }

    h6 {
        font-family: "Miriam Libre";
        font-weight: bold;
        font-size: 10px;
        color: $dark-grey;
        letter-spacing: 0.04px;
        margin-top: 50px;
        margin-bottom: 3px;
    }

    h7 {
        font-family: 'Miriam Libre';
        font-size: 24px;
        color: $dark-grey;
        letter-spacing: 0.31px;
        line-height: 54px;
    }

    p {
        font-family: "Roboto";
        font-weight: 400;
        ;
        font-size: 16px; // color: $dark-grey;
        color: black;
        letter-spacing: 0.31px;
        line-height: 25px;
        margin-bottom: 10px;
    }

    code {
        font-family: 'Overpass Mono', monospace;
        font-size: 15px;
        color: #505050;
        letter-spacing: 0.31px;
        line-height: 25px;
        background-color: $warm-light-grey;
    }

    pre {
        display: block;
        margin-bottom: 18px;
        font-size: 14px;
        line-height: 1.5;
        word-break: break-all;
        word-wrap: break-word;
    }

    pre code {
        border: 1px solid #D8D8D8;
        border-radius: 0;
        padding: 0;
        white-space: pre-wrap;
        background-color: transparent;
        display: block;
        overflow-x: auto;
        background: #F8F8F8;
        padding: 0.5em;
        margin-top: 20px;
    }


    .slideshow {
        width: 95vw;
        margin: 50px 2.5vw 0 2.5vw;
        position: relative;
        right: 25%;
        background-color: $warm-light-grey;
        /*height:485px;*/
        height: 80vh;
    }

    .activity,
    .assignment {
        border-top: 1px solid #D8D8D8;
        border-bottom: 1px solid #D8D8D8;
        background-color: $warm-light-grey;
        margin-top: 90px;
        padding-top: 50px;
        padding-bottom: 50px;
        width: 110vw;
        position: relative;
        left: 50%;
        right: 50%;
        margin-left: -50vw;
        margin-right: -50vw;
    }

    .activity,
    .assignment {
        h1,
        h2,
        p,
        ul,
        ol {
            width: 100%;
            max-width: 768px;
            margin: 0 auto;
            position: relative;
            right: 96px;
        }
    }

    .activity {
        h1::before {
            content: "Activity";
        }
        p,
        ol,
        ul {
            font-family: "Miriam Libre";
            font-weight: bold;
            font-size: 17px;
            color: #505050;
            letter-spacing: 0.31px;
            line-height: 25px;
        }
    }

    .assignment {
        h1::before {
            content: "Assignment";
        }
        h2:first-of-type+p {
            font-family: 'Miriam Libre';
            font-size: 20px;
            color: #505050;
            letter-spacing: 0.31px;
            line-height: 32px;
        }
    }

    .activity,
    .assignment {
        h1 {
            margin-top: 30px;
            margin-bottom: 30px;
            text-align: center;
            color: $dark-grey;
            font-family: "Miriam Libre";
            font-weight: bold;
            font-size: 26px;
            color: #505050;
            letter-spacing: 0.37px;
            text-transform: none;
        }

        h1::before {
            display: block;
            text-align: center;
            font-family: "Miriam Libre";
            font-weight: bold;
            font-size: 10px;
            color: $dark-grey;
            letter-spacing: 2.41px;
            text-transform: uppercase;
            margin-bottom: 25px;
        }

        h1+p:first-of-type {
            text-align: center;
            width: 300px;
            margin: 0 auto;
            margin-bottom: 65px;
            font-family: "Roboto";
            font-weight: 400;
            ;
            font-size: 14px;
            color: #000000;
            letter-spacing: 0.31px;
            line-height: 20px;
        }

        h2 {
            font-family: "Miriam Libre";
            font-weight: bold;
            font-size: 10px;
            color: $dark-grey;
            letter-spacing: 0.04px;
            margin-top: 65px;
            margin-bottom: 18px;
            width: 100%;
            padding-bottom: 3px;
            border-bottom: 0.5px solid #979797;
        }

        img {
            height: auto;
            width: 100%;
        }
    }

    ul {
        list-style-type: none;
        font-family: "Roboto";
        font-weight: 400;
        ;
        font-size: 16px;
        color: black;
        letter-spacing: 0.31px;
        line-height: 25px;
    }

    li {
        margin-bottom: 10px;
    } // ul li{
    //   position:relative;
    //   display:block;
    //   left:30px;
    // }
    ul li:before {
        content: "."; // margin-right: 0.4em;
        position: relative;
        top: -0.1em;
        font-size: 40px;
        line-height: 0;
        right: 30px;
    }
}

dl {
    background-color: $warm-light-grey;
    position: relative;
    left: -20px;
    padding: 20px;
    margin-top: 45px;
    width: 108%;
}

dl dt {
    font-family: 'Miriam Libre';
    font-size: 20px;
    color: $dark-grey;
    letter-spacing: 0.31px;
    line-height: 25px;
    display: inline-block;
}

dl dd {
    display: inline;
    font-family: "Roboto";
    font-weight: 400;
    ;
    font-size: 16px;
    color: black;
    letter-spacing: 0.31px;
    line-height: 25px;
    margin-bottom: 10px;
}




@media only screen and (min-width: 1200px) {
    // .topic-section .slideshow, .topic-section .activity{
    //   width:100vw;
    //   position:relative;
    //   /*left:-327px;*/
    // }
}




@media only screen and (max-width: 900px) {
    .menu-dropdown ul li {
        width: 50%;
    }
    .topic-section .topic-text {
        padding: 0 50px 10px 40px;
    }
    .topic-section .slideshow {
        margin: inherit;
        position: inherit;
        right: inherit;
        width: 100%;
    } // .topic-section .activity{
    //   width:100%;
    // }
}

@media only screen and (max-width: 670px) {
    .menu-dropdown .col-first {
        width: 70%;
        margin-bottom: 25px;
    }
    .menu-dropdown .chapters {
        width: 100%;
    }
}

@media only screen and (max-width: 500px) {
    .menu-dropdown ul li {
        width: 100%;
    }
    .menu-dropdown .col-first {
        width: 85%;
    }
    .topic-section .topic-text {
        padding: 0 20px 10px 20px;
    }
}



